import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Menu } from "antd";
import { useCookies } from "react-cookie";
import {
	SettingOutlined,
	HomeOutlined,
	UnorderedListOutlined,
	TeamOutlined,
	UserOutlined,
	QuestionOutlined,
	FormOutlined,
} from "@ant-design/icons";

function StudyMenu({ currentStudy, currentPath, isPublicView, isSurveyConnected }) {
	const [cookies] = useCookies();
	const currentRole = cookies.role;
	const currentPermission = currentStudy && currentStudy.permission;

	return (
		<>
			<div className="logo" />
			<Menu theme="light" selectedKeys={[currentPath]} mode="inline">
				<Menu.Item key="landing" icon={<HomeOutlined />}>
					<Link to="landing">Landing</Link>
				</Menu.Item>
				{
					!isPublicView && (currentRole === 'instance-admin' || currentPermission === 'admin') && (
						<Menu.Item key="config" icon={<SettingOutlined />}>
							<Link to="config">Study Configuration</Link>
						</Menu.Item>
					)
				}

				{
					!isPublicView && currentRole !== 'patient' && (
						<Menu.Item key="patients" icon={<UserOutlined />}>
							<Link to="patients">Patient</Link>
						</Menu.Item>
					)
				}

				{
					!isPublicView && (currentRole === 'instance-admin' || currentPermission === 'admin') && (
						<Menu.Item key="researchers" icon={<TeamOutlined />}>
							<Link to="researchers">Researchers</Link>
						</Menu.Item>
					)
				}

				{
					!isPublicView && currentRole === 'patient' && (
						<Menu.Item key="survey" disabled={!isSurveyConnected} icon={<FormOutlined />}>
							<Link to="survey">Survey</Link>
						</Menu.Item>
					)
				}
				{
					!isPublicView && (currentRole === 'instance-admin' || currentPermission === 'admin') && (
						<Menu.Item key="5" icon={<UnorderedListOutlined />}>
							Logging
						</Menu.Item>
					)
				}

				{
					!isPublicView && (
						<Menu.Item key="6" icon={<QuestionOutlined />}>
							Help
						</Menu.Item>
					)
				}

			</Menu>
		</>
	);
}

export default StudyMenu;
