import { SET_ALL_STUDIES } from "../actionTypes";
const initalState = {
	allStudies: [],
};

export default function (state = initalState, action) {
	switch (action.type) {
		case SET_ALL_STUDIES: {
			const { allStudies } = action.payload;
			return { ...state, allStudies };
		}
		default:
			return state;
	}
}
