import React, { useState, useEffect } from "react";
import { withRouter, Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";

import { useCookies } from "react-cookie";
import {
	message,
	Card,
	Form,
	Input,
	Button,
	Checkbox,
	Layout,
	Typography,
	Tabs,
	Row,
	Col,
} from "antd";
import {
	UserOutlined,
	LockOutlined,
	SmileOutlined,
	DotChartOutlined,
} from "@ant-design/icons";
import { useTranslation } from "react-i18next";

import { StandardLayout } from "../../Components/Layout";
import styles from "./index.module.scss";
import { useMobile } from "../../Hooks";
import { login, forgotPassword, checkUserInfo } from "../../APIs";
import { serverAxios as axios } from "../../APIs/config";
import { parseJwt } from "../../Utility";
import { userAuthManager } from "../../Service/userAuthManager";
import { tokenManager } from "../../Service/tokenManager";
import { namespace as serviceNamespace } from "../../Service/namespace";
import { setIsLoginCreator, setCurrentUser } from "../../Redux/actions";

const { Content } = Layout;
const { Title } = Typography;
const { TabPane } = Tabs;

const layout = {
	labelCol: {
		span: 5,
	},
	wrapperCol: {
		span: 19,
	},
};
const tailLayout = {
	wrapperCol: {
		span: 24,
	},
};

function Login(props) {
	const [role, setRole] = useState("patient");
	const [forgotMode, setForgotMode] = useState(false);
	const [cookies, setCookie] = useCookies(["isLogin"]);
	const isMobile = useMobile();
	const { t, i18n } = useTranslation(["message"]);

	useEffect(() => {
		if (props.isLogin) {
			window.location = "/my-studies";
		}
	});

	const {
		match: { path, params },
	} = props;
	const config = {
		observationVars: [],
		initFunc: () => {},
	};

	const onFinish = (values) => {
		login({ ...values, user_role: role })
			.then(async (res) => {
				const { access_token, refresh_token } = res.data.result;
				const parsedJwt = parseJwt(access_token);

				tokenManager.setCookies({
					isLogin: true,
					username: values.username,
					role: parsedJwt.user_role,
					access_token: access_token,
					refresh_token: refresh_token,
				});
				props.setIsLoginCreator(true);

				// initiating userAuthManger
				userAuthManager.initRefreshModal();
				userAuthManager.initExpirationLogout();
				tokenManager.refreshToken(access_token);

				const userInfoRes = await checkUserInfo({
					username: values.username,
				});
				if (userInfoRes.status === 200) {
					console.log(userInfoRes.data.result);
					props.setCurrentUser(userInfoRes.data.result);
				}

				// Redirect the user to former page, if they clicked to login page with a link that sends prevPath
				// Otherwise, direct to my-studies page.
				if (props.location.state) {
					props.history.push(props.location.state.prevPath);
				} else {
					console.log(params);
					props.history.push("/my-studies");
				}
				message.success(`Welcome back, ${values.username}`);
			})
			.catch((err) => {
				if (err.response && err.response.status === 409) {
					if (role === "patient") {
						message.error(t("message:login.patient_tab.role"));
					} else {
						message.error(t("message:login.researcher_tab.role"));
					}
				} else if (err.response && err.response.status === 403) {
					message.error(t("message:login.password_error"));
				}
			});
	};

	const onForgotFinish = (values) => {
		forgotPassword(values.email).then((res) => {
			if (res.status === 200) {
				message.success(t("message:login.forgot_password.success"));
			}
		});
	};

	const onFinishFailed = (errorInfo) => {
		console.log("Failed:", errorInfo);
	};

	function onChange(value) {
		setRole(value);
	}

	const forgotPwdCard = (
		<Card>
			<h2 style={{ textAlign: "center" }}>Forgot Your Password?</h2>
			<Form
				{...layout}
				name="basic"
				initialValues={{
					remember: true,
				}}
				onFinish={onForgotFinish}
				onFinishFailed={onFinishFailed}
			>
				<Form.Item
					label="Email"
					name="email"
					rules={[
						{
							required: true,
							message: t("message:login.email.required"),
						},
					]}
				>
					<Input prefix={<UserOutlined />} />
				</Form.Item>

				<Row style={{ marginTop: -15 }}>
					<Col xs={{ span: 0 }} sm={{ span: 5 }}></Col>
					<Col xs={{ span: 24 }} sm={{ span: 19 }}>
						<Form.Item {...tailLayout}>
							<a
								className={styles.forgot}
								onClick={() => {
									setForgotMode(false);
								}}
							>
								 Back to Login
							</a>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={{ span: 0 }} sm={{ span: 5 }}></Col>
					<Col xs={{ span: 24 }} sm={{ span: 19 }}>
						<Form.Item {...tailLayout}>
							<Button
								type="primary"
								htmlType="submit"
								className="login-form-button"
								style={{ width: "100%" }}
							>
								Submit
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Card>
	);

	return (
		<StandardLayout {...config}>
			<Content className={"content"}>
				<div className={styles.container}>
					{!forgotMode ? (
						<Title level={3} className={styles.title}>
							Welcome to CANARIE Patient Portal
						</Title>
					) : null}

					{!forgotMode ? (
						<Card>
							<Tabs
								defaultActiveKey="patient"
								centered
								onChange={onChange}
							>
								<TabPane
									tab={
										<span>
											<SmileOutlined />
											{!isMobile && "I am a "} Patient
										</span>
									}
									key="patient"
								>
									<Row>
										<Col
											xs={{ span: 0 }}
											sm={{ span: 5 }}
										></Col>
										<Col
											xs={{ span: 24 }}
											sm={{ span: 19 }}
										>
											<p>
												New to the platform?{" "}
												<Link to="/registration">
													Register here.
												</Link>
											</p>
										</Col>
									</Row>
								</TabPane>
								<TabPane
									tab={
										<span>
											<DotChartOutlined />
											{!isMobile && "I am a "}Researcher
										</span>
									}
									key="researcher"
								>
									<Row>
										<Col
											xs={{ span: 0 }}
											sm={{ span: 5 }}
										></Col>
										<Col
											xs={{ span: 24 }}
											sm={{ span: 19 }}
										>
											<p>
												Researcher accounts are created
												by invitation only. Please
												contact the instance admin if
												you require credentials.
											</p>
										</Col>
									</Row>
								</TabPane>
							</Tabs>
							<Form
								{...layout}
								name="basic"
								initialValues={{
									remember: true,
								}}
								onFinish={onFinish}
								onFinishFailed={onFinishFailed}
							>
								<Form.Item
									label="Username"
									name="username"
									rules={[
										{
											required: true,
											message: t(
												"message:login.username.required",
											),
										},
									]}
								>
									<Input prefix={<UserOutlined />} />
								</Form.Item>

								<Form.Item
									label="Password"
									name="password"
									rules={[
										{
											required: true,
											message: t(
												"message:login.password.required",
											),
										},
									]}
								>
									<Input.Password prefix={<LockOutlined />} />
								</Form.Item>
								<Row>
									<Col
										xs={{ span: 0 }}
										sm={{ span: 5 }}
									></Col>
									<Col xs={{ span: 24 }} sm={{ span: 19 }}>
										<Form.Item {...tailLayout}>
											<a
												className={styles.forgot}
												onClick={() => {
													setForgotMode(true);
												}}
											>
												Forgot password?
											</a>
										</Form.Item>
									</Col>
								</Row>
								<Row>
									<Col
										xs={{ span: 0 }}
										sm={{ span: 5 }}
									></Col>
									<Col xs={{ span: 24 }} sm={{ span: 19 }}>
										<Form.Item {...tailLayout}>
											<Button
												type="primary"
												htmlType="submit"
											>
												Log In
											</Button>
										</Form.Item>
									</Col>
								</Row>
							</Form>
						</Card>
					) : (
						forgotPwdCard
					)}
				</div>
			</Content>
		</StandardLayout>
	);
}

export default withRouter(
	connect((state) => ({ isLogin: state.isLogin }), {
		setIsLoginCreator,
		setCurrentUser,
	})(Login),
);
