import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
	Layout,
	Typography,
	Row,
	Col,
	Card,
	Space,
	Button,
	Collapse,
	message,
	Tag,
	Modal,
	Avatar
} from "antd";
import { StarOutlined, FormOutlined, } from "@ant-design/icons";
import { useCookies } from "react-cookie";
import { useTranslation } from 'react-i18next';
import { lineBreaker } from '../../../Utility'; 
import AppFooter from "../../../Components/Layout/Footer";
import styles from "./index.module.scss";
import IframeResizer from 'iframe-resizer-react'

const { Content } = Layout;
const { Title, Paragraph } = Typography;
const { Panel } = Collapse;

function Landing(props) {
	function callback(key) {
		console.log(key);
	}
	const [cookies] = useCookies();
	const { isLogin } = cookies;
	const { t, i18n } = useTranslation(['message']);
	const user = useSelector((state) => state.user);
	const surveyQueueLink = useSelector((state) => state.redCapLinks && state.redCapLinks.surveyQueueLink);
	const [surveyModalVisible, setSurveyModalVisible] = useState(false);

	console.log(props);
	const currentStudy = props.currentStudy;

	const panels = [];
	panels.push((
		<Panel
			header="Contact Information"
			key="location"
		>
			<p>{currentStudy && currentStudy.contact && lineBreaker(currentStudy.contact)}</p>
		</Panel>
	));
	panels.push((
		<Panel
			header="Project Investigator"
			key="investigator"
		>
			<p>{currentStudy && currentStudy.investigator}</p>
		</Panel>
	));
	panels.push((
		<Panel
			header="Sponsors and Collaborators"
			key="sponsor"
		>
			<p>{currentStudy && currentStudy.sponsor}</p>
		</Panel>
	));
	

	const onCancel = () => {
		setSurveyModalVisible(false);
	}

	const surveyCard = (
		<Card>
			<Title level={3}>Your Survey Progress</Title>
			{
				currentStudy && currentStudy.useEconsent && (
					<div>
						<Button 
							shape="round" 
							icon={<FormOutlined />} 
							size='large' 
							style={{ marginTop: 10, width: 254 }}
							disabled={props.isSurveyStarted}
						>
							<Link
								to={{
									pathname: `/study/${currentStudy && currentStudy.id}/survey?isSurveyLink=True`,
								}}
								target="_blank"
								style={{ marginLeft: '5px', color: props.isSurveyStarted ? 'grey' : ''}}
							>
								Take eConsent Survey
							</Link>
						</Button>

						<Button 
							shape="round" 
							icon={<FormOutlined />} 
							size='large' 
							style={{ marginTop: 10, width: 254 }}
							disabled={!props.isSurveyStarted}
						>
							<Link
								to={{
									pathname: `/study/${currentStudy && currentStudy.id}/survey?isSurveyLink=False`,
								}}
								target="_blank"
								style={{ marginLeft: '5px', color: !props.isSurveyStarted ? 'grey' : ''}}
							>
								Complete Study Surveys
							</Link>
						</Button>

						<Button 
							shape="round" 
							icon={<FormOutlined />} 
							size='large' 
							style={{ marginTop: 10, width: 254 }}
							onClick={() => setSurveyModalVisible(true)}
						>
							<Link
								style={{ marginLeft: '5px'}}
							>
								Open Study Survey Modal
							</Link>
						</Button>
					</div>
				)
			}
			
		</Card>
	)

	if (currentStudy) {
		const keys = currentStudy.attrs_order;

		for (const key of keys) {
			if (key.startsWith('attr')) {
				const value = currentStudy[key];

				let displayValue = value;
				if (typeof (value) !== 'string') {
					displayValue = value.map((el) => (<Tag>{el}</Tag>));
				}

				panels.push((
					<Panel
						header={key.slice(5).replace('_', ' ')}
						key={key.slice(5)}
					>
						<p>{displayValue}</p>
					</Panel>
				));
			}
		}
	}

	return (
		<>
			<Content className={"content"}>
				<div className={styles.container}>
					<div>
						{isLogin ? null : (
							<Button type="primary" style={{ float: "right" }}>
								<Link
									to={{
										pathname: "/registration",
										state: {
											prevPath: window.location.pathname,
										},
									}}
								>
									Request to Participate
								</Link>
							</Button>
						)}
						<Title level={2}>
							{currentStudy ? currentStudy.name : ""}{" "}
							{isLogin && (
								<Button
									type="link"
									style={{ paddingLeft: "0" }}
								>
									<StarOutlined
										key="star"
										onClick={() =>
											message.success(
												t('message:study.landing.star'),
											)
										}
										style={{
											fontSize: "16px",
											verticalAlign: "baseline",
										}}
									/>
								</Button>
							)}
							<Tag
								color="green"
								style={{ verticalAlign: "middle" }}
							>
								Recruiting
							</Tag>
						</Title>{" "}
					</div>

					<Row gutter={16}>
						<Col
							xs={{ span: 24 }}
							md={{ span: 7 }}
							style={{ marginBottom: "8px" }}
						>
							<Space direction="vertical">
								{
									currentStudy && currentStudy.project_icon ? (
										<Avatar
											shape="square"
											size={270}
											src={currentStudy.project_icon}
										/>
									) : (
										<img
											className={styles.imgContent}
											alt=""
											src={require("../../../Images/study_cover.jpg")}
										></img>
	
									)
								}
								
								{
									user && Object.keys(user).length && user.role === 'patient' && props.isSurveyConnected ? surveyCard : null
								}
								<Card>
									<Title level={3}>Contact Information</Title>
									<Space direction="vertical">
										<Button>
											<a href={currentStudy ? `mailto:${currentStudy.primaryEmail}` : '#'}>
												Email Primary Study Contact
											</a>
										</Button>
										{isLogin ? (
											<Button>
												<a href={currentStudy ? `mailto:${currentStudy.investigatorEmail}` : '#'}>
													Contact Principal Investigator
												</a>
											</Button>
										) : (
											<Button>
												<Link
													to={{
														pathname: "/login",
														state: {
															prevPath:
																window.location
																	.pathname,
														},
													}}
												>
													Contact Study Coordinator
												</Link>
											</Button>
										)}
									</Space>
								</Card>
							</Space>
						</Col>
						<Col xs={{ span: 24 }} md={{ span: 17 }}>
							<Space direction="vertical">
								<Card style={{ minWidth: 800 }}>
									<Title level={3}>Study Description</Title>
									<Paragraph>
										{currentStudy && currentStudy.description && lineBreaker(currentStudy.description)}
									</Paragraph>
									<Collapse
										className={styles.study_description_collapse}
										defaultActiveKey={["1"]}
										onChange={callback}
									>

										{panels}
									</Collapse>
								</Card>
								<Card style={{ minWidth: 800 }}>
									<Title level={3}>
										Eligibility Criteria
									</Title>
									<Paragraph>
										{currentStudy && currentStudy.criteria && lineBreaker(currentStudy.criteria)}
									</Paragraph>
								</Card>
							</Space>
						</Col>
					</Row>
				</div>
				<Modal 
					visible={surveyModalVisible}
					onCancel={onCancel}
					footer={null}
					title="REDCap Surveys"
				>
					<IframeResizer 
						src={surveyQueueLink}
						style={{ minWidth: '100%', }}
						frameborder="0"
						height="370px"
						inPageLinks
						scrolling="auto"
						seamless
					/>
				</Modal>
				<AppFooter />
			</Content>
		</>
	);
}

export default Landing;
